:root {
  --body-font: 'Fira Sans';
  --heading-font: 'Noto Sans';
  --heading-font-weight: 400;
  --body-font-weight: 400;
  --accent-color: #ad2424;
  --second-color: #fefefe;
  --swiper-navigation-size: 20px;
  --swiper-navigation-sides-offset: 0px;
  --swiper-navigation-color: var(--second-color);
  --swiper-navigation-top-offset: calc(100% - 10px);
  --swiper-pagination-bottom: 0px;
  --swiper-pagination-color: var(--second-color);
}

* {
  box-sizing: border-box;
}

h1 {
  font-weight: normal;
  font-size: 2.4em;
  margin: 12px 0;
  line-height: 1.2em;
}

body {
  font-family: var(--heading-font);
  margin: 0;
  padding: 0;

  color: var(--accent-color);
}

.nav {
  background: var(--second-color);
  color: var(--accent-color);
  padding: 10px 0;
  text-align: center;
}

.nav a {
  color: var(--accent-color);
  margin: 0 10px;
  text-decoration: none;
}

.header {
  padding: 6px 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: var(--accent-color);
  position: sticky;
  top: 0;
  z-index: 5;
  box-shadow: 0 2px 0px 0px white;
}

.header a {
  color: var(--second-color);
  text-decoration: none;
}

.nav-contacts .contacts_icons {
  display: none;
}

footer {
  text-align: center;
  padding: 20px 0;
  background: var(--second-color);
  color: var(--accent-color);
}

.section {
  display: flex;
  flex-direction: column;
  color: var(--second-color);
}

/* .section:nth-child(2n) .content {
            clip-path: polygon(0 0, 100% 40px, 100% 100%, 0 calc(100% - 40px));
        } */

.section__white {
  padding-bottom: 42px;
}

.section__white .content {
  background-color: var(--second-color);
}

.section__main .photo {
  margin-top: -40px;
}

.section__main .content {
  clip-path: none;
  min-height: auto;
  margin-bottom: 10px;
}

.section__features .content {
  margin-bottom: -94px;
  min-height: calc(100vh - 260px);
}

.content {
  padding: 42px 20px;

  /* min-height: calc(100vh - 260px); */
  margin-bottom: 20px;
  background-color: var(--accent-color);
}

section .content {
  padding: 42px 20px;
}

.img img {
  display: block;
  max-width: 100%;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.bold {
  font-weight: bold;
}

.list {
  padding-left: 20px;
}

.logo {
  width: 140px;
}

.links {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.photo {
  clip-path: polygon(0 40px, 100% 0, 100% calc(100% - 40px), 0 100%);
  /* margin: 0 -20px; */
  height: 220px;
  margin-top: -64px;
  margin-bottom: -42px;
  z-index: 1;
}

.section:nth-child(2n) .photo {
  clip-path: polygon(0 0, 100% 40px, 100% 100%, 0 calc(100% - 40px));
}

